import { getAnimeDetail } from '@/lib/api' // pakai ZORO_API_BASE lokal
import { Navbar } from '@/components/navbar'
import Image from 'next/image'
import { Button } from '@/components/ui/button'
import { Play, Heart, Share2, Star, Calendar, Film, Activity } from 'lucide-react'
import { notFound } from 'next/navigation'

interface AnimePageProps {
  params: { id: string } // ID Zoro/slug string
}

export default async function AnimePage({ params }: AnimePageProps) {
  const { id } = params
  let anime = null
  try {
    anime = await getAnimeDetail(id)
  } catch (err) {
    console.error('Error fetching anime:', err)
    notFound()
  }

  if (!anime) notFound()

  // Gunakan properti yang ada di Zoro API lokal
  const imageUrl = anime.image || '/placeholder.svg'
  const title = anime.title || '-'
  const episodes = anime.episodes ?? 'TBA'
  const releaseDateRaw = 'TBA'
  const releaseYear = 'TBA'
  const fullAirDate = 'TBA'
  const score = '-' // tidak ada di API lokal
  const status = '-' // tidak ada di API lokal
  const genres = [] as { name: string }[] // kosong karena API lokal tidak punya genres
  const synopsis = '-' // default karena API lokal tidak punya synopsis

  return (
    <main className="min-h-screen bg-background overflow-x-hidden">
      <div className="fixed top-0 left-0 w-full z-50">
        <Navbar />
      </div>

      {/* HERO BACKGROUND */}
      <div className="relative h-112 md:h-128 w-full overflow-hidden">
        <Image
          src={imageUrl}
          alt="Anime background"
          fill
          className="object-cover blur-sm"
          quality={80}
          unoptimized
        />
        <div className="absolute inset-0 bg-gradient-to-t from-black/40 dark:from-black/20 via-transparent to-transparent"></div>
      </div>

      {/* CONTENT WRAPPER */}
      <div className="relative z-20 max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 -mt-24 md:-mt-40 grid grid-cols-1 md:grid-cols-3 gap-8">
        {/* POSTER */}
        <div className="relative w-44 md:w-60 mx-auto md:mx-0">
          <div className="shadow-2xl rounded-lg overflow-hidden group transition-transform duration-500 hover:scale-105">
            <Image
              src={imageUrl}
              alt={title}
              width={300}
              height={400}
              className="w-full h-auto"
              unoptimized
            />
            <div className="absolute inset-0 bg-black/10 group-hover:bg-black/20 transition-all"></div>
          </div>
        </div>

        {/* RIGHT TEXT SECTION */}
        <div className="md:col-span-2 text-center md:text-left flex flex-col justify-center gap-4">
          <h1 className="text-3xl md:text-4xl font-bold text-white drop-shadow-lg">
            {title}
          </h1>

          {/* STATS GRID */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-4">
            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <Star className="text-yellow-400" />
              <span className="text-sm text-muted-foreground">Rating</span>
              <span className="text-lg font-bold text-primary">{score}</span>
            </div>

            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <Film className="text-blue-500" />
              <span className="text-sm text-muted-foreground">Episodes</span>
              <span className="text-lg font-bold text-primary">{episodes}</span>
            </div>

            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <Activity className="text-green-500" />
              <span className="text-sm text-muted-foreground">Status</span>
              <span className="text-lg font-bold text-primary">{status}</span>
            </div>

            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <Calendar className="text-purple-500" />
              <span className="text-sm text-muted-foreground">Tahun Rilis</span>
              <span className="text-lg font-bold text-primary">{releaseYear}</span>
            </div>
          </div>

          {/* ACTION BUTTONS */}
          <div className="flex flex-wrap gap-3 mt-4 justify-center md:justify-start">
            <Button className="bg-primary text-white gap-2 hover:bg-primary/90">
              <Play size={18} /> Watch Now
            </Button>

            <Button variant="outline" className="gap-2 border-primary text-primary hover:bg-primary/15 dark:hover:bg-primary/25">
              <Heart size={18} /> Add to Favorites
            </Button>

            <Button variant="outline" className="gap-2 border-primary text-primary hover:bg-primary/15 dark:hover:bg-primary/25">
              <Share2 size={18} /> Share
            </Button>
          </div>
        </div>
      </div>

      {/* GENRES */}
      {genres.length > 0 && (
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 mt-12 mb-6 flex flex-wrap gap-3">
          {genres.map((genre: { name: string }) => (
            <span key={genre.name} className="px-3 py-1 bg-card border border-primary text-primary text-sm rounded-full">
              {genre.name}
            </span>
          ))}
        </div>
      )}

      {/* SYNOPSIS */}
      <section className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 mb-12">
        <h2 className="text-2xl font-bold text-foreground mb-4">Synopsis</h2>
        <p className="text-muted-foreground leading-relaxed">{synopsis}</p>
      </section>

      {/* AIR DATES */}
      <section className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 mb-12">
        <h2 className="text-2xl font-bold text-foreground mb-4">Tanggal Rilis</h2>
        <p className="text-muted-foreground">{fullAirDate} - Ongoing</p>
      </section>
    </main>
  )
}
